    }
}
.plane * {
    -webkit-transition: 1s;
    transition: 1s;
}
.plane {
    block-size: border-box;
    position: relative;
    top:0;
    left: 0;
    height: 90vh;
    width: 100%;
    overflow: hidden;
}

.plane > .controllers {
    position: absolute;
    height: 100%;
    width: 100%;
    overflow: hidden;
}

.plane > .controllers > .prev-button-wrapper {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    left: -15%;
    width: 30%;
    height: 600px;
    -webkit-transition: 1s;
    transition: 1s;
}


.plane > .controllers > .prev-button-wrapper:hover {
    left: 0px;
}


.plane > .controllers > .prev-button-wrapper > .prev-button {
    display: inline-block;
    border-radius: 50%;
    background-image: url(/engl1102/static/media/previous.f5990a79.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 50px;
    height: 50px;
    position: absolute;
    left: 20%;
    top: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}

.plane > .controllers > .next-button-wrapper {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    right: -15%;
    width: 30%;
    height: 600px;
    -webkit-transition: 1s;
    transition: 1s;
}


.plane > .controllers > .next-button-wrapper:hover {
    right: 0px;
}


.plane > .controllers > .next-button-wrapper > .next-button {
    display: inline-block;
    border-radius: 50%;
    background-image: url(/engl1102/static/media/next.0fd61cb4.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 50px;
    height: 50px;
    position: absolute;
    right: 20%;
    top: 50%;
    -webkit-transform: translate(+50%,-50%);
            transform: translate(+50%,-50%);
}
body {
    font-family: Arial,sans-serif;
    margin:0;
    overflow-x: hidden;
}

#particle-wrapper {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height:100%;
    background-color: #333333;
    z-index: -30;
}

#header-wrapper {
    position: -webkit-sticky;
    position: sticky;
    padding: 0;
    left: 0;
    top: 0;
    z-index: 30;
    width: 100%;
    background-color: #111111;
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,1);
}

.header {
    display: inline-block;
    margin: 0 0 -4px 0;
    padding: 0;
}

.header-small-screen {
    display: none;
}

#header-wrapper li {
    float: left;
    list-style-type: none;
    border: none;
    position: relative;
    margin: 0 auto;
    -webkit-transition: 1s;
    transition: 1s;
}

#header-wrapper li:hover {
    background-color: grey;
}

#header-wrapper li a {
    display: block;
    text-decoration: none;
    color: white;
    text-align: center;
    padding: 20px;
}

#header-wrapper li .c {
    display: none;
    position: absolute;
    background-color: #efefef;
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,1);
    z-index: 1;
    width: 350px;
}

#header-wrapper li:hover .c {
    display: block;
}

#header-wrapper li .c a{
    color: #333333;
}

.content {
    display: block;
    color: #333;
    padding: 20px;
}

.content:link {
    color: #333;
}

.content:hover {
    color: #333;
    background-color: #ddd;
}

.content:active {
    color: #333;
    background-color: lightgrey;
}

.content:visited {
    color: #333;
}

.white-section {
    background-color: #efefef;
    color: #333333;
    padding: 24px 5vw 24px 5vw;
}

#about {
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

#video-wrapper {
    flex: 0 1;
    box-sizing: border-box;
}

#intro {
    padding-left: 24px;
    padding-right: 24px;
    flex: 1 1 400px;
    box-sizing: border-box;
    min-width: 280px;

}
video {
    padding-left: 24px;
    padding-right: 24px;
    display: block;
    margin: auto;
    max-height: 40vh;
}

.black-section {
    background-color: transparent;
    color: #ffffff;
    padding: 24px 5vw 24px 5vw;
    text-shadow: 0 0 5px #333333;
}

#column-wrapper {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

#column {
    flex: 1 1;
    flex-basis: 50%;
}
#column * {
    text-align: center;
}

#cs-picture-wrapper{
    display: flex;
    justify-content: center;
    align-items: stretch;
    flex-wrap: wrap;
}

#cs-picture-wrapper img {
    box-shadow: 0px 0px 5px 0px #efefef;
}

#copyright {
    padding: 0;
    margin: 0;
    text-align: center;
}

#copyright p {
    padding: 2px;
    margin: 0;
}

#welcome {
    text-align: center;
    padding-top: 30vh;
    padding-bottom: 30vh;
    font-family: 'Dancing Script', fantasy;
}

#welcome h1 {
    font-size:64px;
}

@media only screen and (max-width: 1366px) {
    video {
        max-height: 45vh;
    }

    #welcome h1 {
        font-size:56px;
    }
}
@media only screen and (max-width: 1000px) {
    video {
        max-height: 52vh;
    }

    #welcome h1 {
        font-size:48px;
    }
}

@media only screen and (max-width: 780px) {
    #column {
        width: 100vw;
    }

    .header {
        display: none;
    }

    .header-small-screen {
        display: inline-block;
        margin: 0 0 -4px 0;
        padding: 0;
    }
    #intro {
        max-width: 100%;
    }

    #intro h1 {
        text-align: center;
    }

    #header-wrapper li {
        width: 100%;
    }

    video {
        max-height: 60vh;
    }

    #welcome h1 {
        font-size:40px;
    }
}

@media only screen and (max-height: 500px) {
    video {
        max-height: 75vh;
    }
    #welcome h1 {
        font-size:32px;
    }
}

.foldable {
    margin: 0;
    width: 100%;
    box-sizing: border-box;
}


.foldable .operator {
    display: flex;
    justify-content: space-between;
}

.foldable h2 {
    display: inline-block;
}


.foldable .expand *{
    -webkit-transition: 1s;
    transition: 1s;
}

.foldable p {
    z-index: -1;
}

